import styled from 'styled-components'

const StyledP = styled.p`
  font-size: 1.3rem;
  font-family: medium-content-serif-font,Georgia,Cambria,"Times New Roman",Times,serif;
  letter-spacing: 0.03rem;
  display: inline-flex;
  align-items: center;
  line-height: 1.5;
`

export default StyledP



